import React from "react";
import styled from 'styled-components';


const ContentFooter = styled.h1`
    font-size: 2em;
    color:#fff;
    text-align:right;
`;

const ContentFooterBg = styled.footer`
  background:#4B0D2B;
  position:absolute;
  z-index:-1;
  height:50px;
  width:100%;
`;

const ContentSocial = styled.ul`
  display:flex;
  justify-content: space-evenly;
`;

const HandCrafted = styled.p`
  display: flex;
  justify-content: center;
  color:#fff;

`;

class Footer extends React.Component {
    render() {
        return (
            <ContentFooterBg className="BottomDiv ">
                <div className="grid-container">
                    <div className="grid-x grid-margin-x">
                        <div className="cell small-6 large-4">
                            <ContentFooter>Follow me on:</ContentFooter>
                        </div>
                        <div className="cell small-6 large-8">
                            <ContentSocial>
                                <li><a href="">Github</a></li>
                                <li><a href="">Gitlab</a></li>
                                <li><a href="">Bitbucket</a></li>
                                <li><a href="">Instagram</a></li>
                                <li><a href="">Linkedin</a></li>
                            </ContentSocial>
                        </div>
                    </div>
                    <HandCrafted>Handcrafted by me &#9400; ivans.tech</HandCrafted>
                </div>
            </ContentFooterBg>
        );
    };
}

export default Footer;


