(() => { 

  setTimeout(function(){ 
        const preloderBg = document.querySelector('.preloder');
        const body = document.getElementsByTagName("body")[0];
        const bottomDiv = document.querySelector(".BottomDiv");
        const TopSection = document.querySelector('.TopSection');
        bottomDiv.classList.add('staticElement');
        preloderBg.style.display = "none";
        bottomDiv.classList.add('staticElement');
        TopSection.classList.add('staticElement');
        body.classList.add('newBody');
    }, 7000);

 })();


