import React from "react";
import Preloder from './Preloder';
import Header from './Header';
import Footer from './Footer';



class App extends React.Component {

    render() {
        return (
            <div className="whole">
            <Header />
            <div className="preloder">
               
             <Preloder/>

            </div>

        <Footer />
        </div>
    

        );

    }

}

export default App;


